// src/application.ts
import { Application as PixiApp } from "pixi.js";
import {
  BaseApplication,
  withTick,
  withResize,
  withInteraction
} from "sketch-application";
var PixiApplication = class extends BaseApplication {
  constructor(canvas, pixiOptions = {}) {
    super(canvas);
    this.pixi = new PixiApp({
      resolution: 1,
      autoDensity: false,
      view: canvas,
      ...pixiOptions
    });
  }
};
var Application = withInteraction(withTick(withResize(PixiApplication)));

// src/index.ts
import { attach } from "sketch-attach";
function sketch(opts, pixiOptions) {
  const { canvas } = attach(opts);
  const app = new Application(canvas, pixiOptions);
  app.on({
    type: "resize",
    action: ({ app: app2 }) => {
      app2.pixi.renderer.resize(app2.canvas.width, app2.canvas.height);
    }
  });
  return app;
}
export {
  PixiApplication,
  sketch
};
